ía, chalecos salvavidas y también en materiales aislantes. Es ligero en cuanto a su peso y es impermeable.")])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"El lino"}},[_c('div',{staticClass:"col-lg-5 col-md-8 mb-4 mb-lg-0"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/linea-img3.png"),"alt":""}})]),_c('div',{staticClass:"col-lg-6"},[_c('h5',[_vm._v("El lino")]),_c('p',[_vm._v("Pertenece a la familia de las lináceas herbáceas, también considerada una de las primeras fibras que el hombre abstrajo y produjo para indumentaria en el periodo Neolítico. Con el pasar del tiempo cobra especial relevancia dadas sus características en la confección de ropa exterior e interior, ropa de cama, toallas, entre otros; es al igual que el algodón muy usado en épocas de verano. ")]),_c('p',[_vm._v("Se caracteriza por ser una fibra cuya longitud llega a ser entre 20 cm y 40 cm, permitiendo una hilatura fina. Es lisa, resistente, rígida y se arruga fácilmente. Es un material fresco, absorbe y retienen el agua entre el 50 % y 60 % de su peso. En procesos de tinturado es permeable y propicio para la estampación.")])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo":"El cáñamo"}},[_c('div',{staticClass:"col-lg-5 col-md-8 mb-4 mb-lg-0"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/linea-img4.png"),"alt":""}})]),_c('div',{staticClass:"col-lg-6"},[_c('h5',[_vm._v("El cáñamo")]),_c('p',[_vm._v("Es una fibra muy resistente, fresca, absorbente y duradera, conduce el calor y funciona muy bien a los tintes, repele el moho y bloquea la luz ultravioleta, tiene atributos naturales antibacterianos. ")]),_c('p',[_vm._v("Suele ser muy similar al lino, usado también en tapicería y otras que no necesariamente se ciñen al campo textil. También suelen combinar esta fibra con el algodón, lino seda y lana para brindar mayor resistencia a la estructura textil. ")])])]),_c('div',{staticClass:"row justify-content-center",attrs:{"titulo"